<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <div id="map" style="width:100%;height:100%;"></div>
      <div class="return" @click="close">
        <div style="background-image: url('static/image/header/return.png')"></div>
      </div>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      show: false,
      map: undefined
    }
  },
  watch: {
    show(val) {
      if (val) {
        setTimeout(() => {
          this._initMap()
        }, 1000)
      } else {
        console.log('销毁地图')
        // this.map && this.map.destroy()
      }
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    },
    _initMap() {
      console.log('地图')
      let mapOptions = {
        center: {lat: Number(this.details.Latitude), lng: Number(this.details.Longitude)}, // 地图的中心点
        zoom: 15, // 地图缩放比例
        // mapTypeId: mapType, // 指定地图展示类型：卫星图像、普通道路
        scrollwheel: true, // 是否允许滚轮滑动进行缩放
        disableDefaultUI: true
      }
      console.log('window: ', window)
      this.map = new window.google.maps.Map(document.getElementById('map'), mapOptions)
      let markerStart = new window.google.maps.Marker({
        position: mapOptions.center
        // icon: '' 自定义图标
      })
      markerStart.setMap(this.map)
      let infowindow = new window.google.maps.InfoWindow({ content: `${this.language === 'CN' && this.details.NameChn ? this.details.NameChn : this.details.NameEng}` })
      markerStart.addListener('click', function() { infowindow.open(this.map, markerStart) })
      infowindow.open(this.map, markerStart)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  top 0
  left 0
  background-color $color_white
  z-index 10
  >.return
    @extend .flex-center
    width 30px
    height 30px
    background-color $color_white
    border-radius 15px
    box-shadow 0 0 20px $color_gray_text
    position absolute
    top 20px
    left 20px
    >div
      width 20px
      height 20px
      background-size 100% 100%
</style>



